import React from "react";
import Styled from "../Styled/MovieStyled.module.css";
import Movie from "./Movie.jsx";
import { useState, useEffect } from "react";
import { useRef } from "react";

const MovieList = (props) => {
  // console.log(props.API_Endpoint);
  // L·∫•y t·ªça ƒë·ªô X v√† Y
  const [ScrollRight, setScrollRight] = useState({ x: 0, y: 0 });
  const test = useRef();
  const HandleMouseMove = (event) => {
    // B·∫Øt t·ªça ƒë·ªô x v√† Y c·ªßa chu·ªôt. Sau ƒë√≥ thay ƒë·ªïi gi√° tr·ªã state, gi√° tr·ªã state ƒë∆∞·ª£c d√πng ƒë·ªÉ thay ƒë·ªïi v·ªã tr√≠ slideimage khi di chuy·ªÉn chu·ªôt
    setScrollRight({
      x: event.clientX,
      y: event.clientY,
    });
    // Khi di chuy·ªÉn chu·ªôt s·∫Ω t·∫°o hi·ªáu ·ª©ng l∆∞·ªõt
    if (ScrollRight.x > 0) {
      test.current.scrollLeft += 5;
    }
    if (ScrollRight.x < 0) {
      test.current.scrollLeft -= 20;
    }
  };
  // console.log(ScrollRight.x);
  return (
    // D√πng ƒë·ªÉ t·∫°o c√°c ·∫£nh d·ªçc v√† ·∫£nh ngang
    <ul className={Styled.Movie_style} onDrag={HandleMouseMove} ref={test}>
      {props.API_Endpoint
        ? props.API_Endpoint.map((movie) => (
            <Movie
              key={movie.id}
              id={movie.id}
              title={movie.title}
              releaseDate={movie.releasedate}
              openingText={movie.overview}
              image={movie.posterpath}
              imageback={movie.backdrop_path}
              Description={movie.overview}
              vote={movie.voteaverage}
            />
          ))
        : props.movies.map((movie) => (
            <Movie
              key={movie.id}
              id={movie.id}
              title={movie.title}
              releaseDate={movie.releasedate}
              openingText={movie.overview}
              image={movie.backdrop_path}
              Description={movie.overview}
              vote={movie.voteaverage}
            />
          ))}
    </ul>
  );
};

export default MovieList;
