import React from "react";
import { Link } from "react-router-dom";
import SearchIcon from "./SearchIcon";
import { useEffect } from "react";
import { useState } from "react";
import Styled from "../Styled/Navbar.module.css";

const Navbar = (props) => {
  const [scroll, setScroll] = useState(0);
  // const [event, SetEvent] = useState(true);
  // Thay ƒë·ªïi class khi thay ƒë·ªïi t·ªça ƒë·ªô. Tr·∫°ng th√°i class scroll v√† tr·∫°ng th√°i class Scroll_state2
  const classess = `${scroll > 100 ? Styled.Scroll : Styled.Scroll_state2}`;

  const handleSearch = () => {
    window.location.assign("/Search/"); // Quay l·∫°i trang Search
  };
  useEffect(() => {
    const onScroll = () => setScroll(window.pageYOffset); // L·∫•y s·ªë li·ªáu t·ª´ vi·ªác scroll
    // clean up code
    // web s·∫Ω l·∫Øng nghe v√† th·ª±c hi·ªán scroll xu·ªëng s·ªë li·ªáu m√¨nh ƒëang c√≥
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true }); // scroll xu·ªëng s·ªë li·ªáu ƒë√£ ƒë∆∞·ª£c l∆∞u ·ªü onScroll
    return () => window.removeEventListener("scroll", onScroll); // Tr√°nh l·∫∑p s·ª± ki·ªán
  }, []);
  // console.log(scroll);
  return (
    <div className={classess}>
      <div className={Styled.navbar}>
        <Link to="/">Movie App</Link>
        <SearchIcon onClick={handleSearch} />
      </div>
    </div>
  );
};

export default Navbar;
