import React, { useState } from "react";
import Authcontext from "./useHook-Custom/context";
import Navbar from "./Components/Navbar";
// import Styled from "./Styled/Navbar.module.css";
import DataHttp from "./Components/DataHttp";
import MovieData from "./Section/MovieData";
import Banner from "./Components/Banner";

function Browse(props) {
  // Bi·∫øn l·∫•y d·ªØ li·ªáu trailer movie
  const [DataContext, setDataContext] = useState({
    Name: "",
    id: 0,
    date: "",
    overview: "",
    vote: "",
    image: "",
    imageback: "",
  });
  const [showmovie, setShowmovie] = useState(false);
  // Bi·∫øn l∆∞u d∆∞ li·ªáu movie sau khi l·∫•y random
  const [sendData, setSendData] = useState({
    name: "",
    overview: "",
    id: 0,
  });
  let a;
  // H√†m c√≥ ch·ª©c nƒÉng hi·ªán modal
  const showSummary = (item) => {
    // console.log(DataContext);
    setShowmovie(true);
  };
  // H√†m c√≥ ch·ª©c nƒÉng ·∫©n modal
  const hideSummary = () => {
    setShowmovie(false);
  };
  // D·ªØ li·ªáu phim ng·∫´u nhi√™n ƒë∆∞·ª£c truy·ªÅn xu·ªëng http ƒë·ªÉ l·∫•y random movie
  const Propsmovie = (Virus) => {
    setSendData({
      name: Virus.name,
      overview: Virus.overview,
      image: Virus.image,
      id: Virus.id,
    });
  };
  return (
    <Authcontext.Provider
      value={{
        MovieCTX: DataContext, // M·∫£ng truy·ªÅn d·ªØ li·ªáu t·ª´ trailer movie
        onShow: showSummary,
        onHide: hideSummary,
        onReceive: setDataContext, // H√†m l·∫•y d·ªØ li·ªáu t·ª´ trailer movie
      }}
    >
      <Banner onData={sendData} />
      <Navbar />
      <DataHttp onProps={Propsmovie} />
      {showmovie && <MovieData onClose={hideSummary} />}
      {/* {console.log(showmovie)} */}
    </Authcontext.Provider>
  );
}

export default Browse;
