import React from "react";
import { useRef } from "react";
import { useState } from "react";
import SearchIcon from "../../browse/Components/SearchIcon";
import AddNameFilm from "../Store/Context-search";
import Styled from "../Styled/MovieSearch.module.css";

const SearchBox = (props) => {
  const [addnamemovie, setAddNameMovie] = useState("");
  const Searchvalue = useRef(); // Theo d√µi thanh input
  const Sendrequest = async (event) => {
    event.preventDefault(); // Tr√°nh th·ª±c hi·ªán t√°c v·ª• g·ª≠i cho server, ƒëi·ªÅu n√†y s·∫Ω g√¢y m·∫•t d·ªØ li·ªáu,reload l·∫°i trang
    const enteredMovie = await Searchvalue.current.value; // L√°y d·ªØ li·ªáu t·ª´ √¥ input
    if (enteredMovie.trim().length === 0) {
      // valid value c·ªßa input, n·∫øu sai s·∫Ω ng·ª´ng th·ª±c hi·ªán c√°c code b√™n d∆∞·ªõi
      return;
    }
    console.log(`Dang load phim ${enteredMovie}`);
    setAddNameMovie(enteredMovie); // L∆∞u tr·ªØ t√™n phim ƒë·ªÉ chuy·ªÉn l√™n API t√¨m ki·∫øm
    // ...
    Searchvalue.current.value = "";
  };
  // Th·ª±c hi·ªán x√≥a input khi nh·∫•n Search
  const Reset = (event) => {
    event.preventDefault();
    Searchvalue.current.value = "";
  };
  return (
    // truy·ªÅn t√™n phim cho context hook, t·ª´ ƒë√≥ ResultList s·∫Ω l·∫•y ƒë∆∞·ª£c name movie v√† t√¨m ki·∫øm
    <AddNameFilm.Provider value={addnamemovie}>
      <form className={Styled.Box} onSubmit={Sendrequest}>
        <div>
          <input
            ref={Searchvalue}
            type="search"
            name="searchmovie"
            placeholder="Ex: Thor"
          ></input>
          <SearchIcon />
        </div>
        <div className={Styled.line}></div>
        <button type="button" className={Styled.reset} onClick={Reset}>
          RESET
        </button>
        <button type="submit" className={Styled.search}>
          SEARCH
        </button>
      </form>
      {props.children}
    </AddNameFilm.Provider>
  );
};

export default SearchBox;

